.nav-bar{width:1250px;background:#d2691e;padding:15px;display:flex;justify-content:space-between;align-items:center}.nav-bar .more-options:hover{cursor:pointer;color:#fff}.nav-bar .logo{display:flex;flex-direction:column;transition:.5s ease-in-out}.nav-bar .logo::after{position:absolute;content:"Load Random Movies";margin-top:80px;margin-left:15px;transform:rotate(-5deg);visibility:hidden}.nav-bar .logo:hover{cursor:pointer;transform:translateY(-20px)}.nav-bar .logo:hover::after{visibility:visible;transform:translateY(-25px)}.nav-bar .floating-box{position:fixed;background:rgba(0,0,0,.7);top:0px;left:300px;padding:320px 200px;z-index:5}.nav-bar .floating-box .help{background:#fff;padding:25px;border-radius:20px}
.movie-card{padding:5px;background:#615f5f;background:linear-gradient(0deg, #615f5f 0%, #a2a2a2 49%, #555353 100%)}.movie-card .a2f{position:absolute;margin-top:385px;margin-left:235px;color:#5f9ea0;z-index:1}.movie-card .title{background:rgba(0,0,0,.493);padding:5px;position:absolute;margin-top:-35px;color:#fff;width:290px}
.data-field{width:1280px;background:#292828;display:flex;flex-direction:column;align-items:center}.data-field .top-btns input{font-size:17px;padding:10px 15px;border-radius:20px}.data-field .movie-display{padding:3px;display:grid;grid-template-columns:310px 310px 310px 310px;grid-gap:3px 10px;gap:3px 10px}.data-field .footer-area{background:#000;color:#fff;display:flex;justify-content:center;align-items:center}.data-field .btn-def{color:#fff;border:unset;margin:5px;padding:15px;border-radius:20px;background:#d2691e}.data-field .btn-def:hover{cursor:pointer;background:#53321a}
